            </el-select>
          </el-form-item>
          <el-form-item label="来源" size="mini">
            <el-select clearable v-model="params.form_type">
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="WMS" :value="1">WMS</el-option>
              <el-option label="ERP" :value="2">ERP</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="商品编号" size="mini">
            <el-input v-model="params.goods_id"></el-input>
          </el-form-item>
          <el-form-item label="商品名称" size="mini">
            <v-GoodsList :scopes='{row: params }' :vxeParams='{company_id: params.company_id}' :showObj="{goods_id: '商品编号' , goods_name: '商品名称'}" />
          </el-form-item>
          <el-form-item label="调出仓库" size="mini">
            <el-select clearable v-model="params.warehouse_id" placeholder="请选择" filterable :loading="paramsLoading" @focus="getWarehouseFouse" @change="params.allocation_id = ''">
              <el-option :label="item.warehouse_name" :value="item.warehouse_id" :key="item.warehouse_id" v-for="item in warehouseList"></el-option>
            </el-select>
          </el-form-item>
