          </el-form-item>
          <el-form-item label="调入仓库" size="mini">
            <erpWarehouse v-model="params.after_warehouse_id" :searchParams='{ company_id: params.company_id }' @change="params.after_allocation_id = ''"></erpWarehouse>
          </el-form-item>
          <el-form-item label="调入货位" size="mini">
            <erpAllocation v-model="params.after_allocation_id" :disabled='!params.after_warehouse_id' :searchParams="{ warehouse_id: params.after_warehouse_id, is_incomplete: 3}" />
          </el-form-item>
          <el-form-item label="年周/批次" size="mini">
            <erpBatch v-model="params.batch_id" :fatherParams="params" :searchParams="{ goods_id: params.goods_id,warehouse_id: params.warehouse_id,allocation_id: params.allocation_id, company_id: params.company_id}" />
          </el-form-item>
          <el-form-item size="mini" class="search-style">
            <el-button @click="onSearch(1)" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet">
        <el-dropdown style="margin:0 10px;" v-if="authority.b_export==1">
          <span split-button style="cursor:pointer;color:#4e90e2">
            <el-button plain icon="iconfont icon-daochu10" size="mini">导出</el-button>
          </span>
          <el-dropdown-menu slot="dropdown" style="margin-top:10px;">
            <el-dropdown-item @click.native="exportOrder(1)">导出Excel文件</el-dropdown-item>
            <el-dropdown-item @click.native="exportOrder(2)">导出CSV逗号分隔</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <div class="table_container">
        <xTable :isShowTool='true' :ajax='{ url: "DefectiveProductGetOrderDetailed", params: params }' ref='xTable' :footerMethod='footerMethod' @getData='getTableData' :tableColumn='tableColumn' />
      </div>
    </div>
  </div>
</template>

<script>
import { tableHeader } from "./config/ScheduleIncomplete";
import {
  CompanySwitchClear,
  opthionList
} from "common/utils/public_function/commonUtils";
export default {
  name: "ScheduleIncomplete",
  mixins: [tableHeader],
  data() {
    return {
      timeValue: [],
      AuditDate: [],
      loadingsum: true,
      loading:false,
      TableDataSum: {},
      warehouseLists: [],
      allocationLists: [],
      paramsLoading: false,
      params: {
        order_type: 0,
        company_id: Number(localStorage.company_id),
        company_name: localStorage.getItem('company_name')
      }
    };
  },
  methods: {
    companyChange(val) {
      CompanySwitchClear(this, this.params);
      this.params.after_warehouse_id = "";
      this.params.after_allocation_name = "";
    },
    getWarehouseFouse(val) {
      let item = !(val instanceof Object);
      this.$set(this.params, item ? "after_warehouse_id" : "warehouse_id", "");
      this.paramsLoading = true;

      this.$axMethod
        .getWarehouseFocus({
          company_id: item
            ? this.params.after_company_id
            : this.params.company_id,
          type: item ? 1 : ""
        })
        .then(({ data }) => {
          if (data.code == 200) {
            if (item) {
              this.$set(this, "warehouseLists", data.data);
            } else {
              this.$set(this, "warehouseList", data.data);
            }
            this.paramsLoading = false;
          }
        });
    },
    getTableData(data) {
      this.TableDataSum = data.data.data.desc ? data.data.data.desc : {};
    },
    allocationFocus(val) {
      this.paramsLoading = true;
      let item = !(val instanceof Object);
      this.$set(
        this.params,
        item ? "after_allocation_id" : "allocation_id",
        ""
      );
      this.$axMethod
        .allocationInventoryList({
          warehouse_id: item
            ? this.params.after_warehouse_id
            : this.params.warehouse_id
        })
        .then(({ data }) => {
          if (data.code == "200") {
            if (item) {
              this.$set(this, "allocationLists", data.data);
            } else {
              this.$set(this, "allocationList", data.data);
            }
            this.paramsLoading = false;
          } //货位列表下拉
        });
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.timeValue = [];
      this.AuditDate = [];
      this.$set(this, "params", {
        order_type: 0,
        company_id: Number(localStorage.company_id)
      });
      this.onSearch(1);
    },
    onSearch(number) {
      if (this.AuditDate) {
        this.params.start_auditor_time = this.AuditDate[0];
        this.params.end_auditor_time = this.AuditDate[1];
      } else {
        this.params.start_auditor_time = this.params.end_auditor_time = "";
      }
      if (this.timeValue) {
        this.params.start_business_time = this.timeValue[0];
        this.params.end_business_time = this.timeValue[1];
      } else {
        this.params.start_business_time = this.params.end_business_time = "";
      }
      this.$refs.xTable._getData(number);
    },
    exportOrder(scheduleType) {
      // 导出库存收发明细表导出URL
      let s = Object.assign(this.params, {
        export_type: scheduleType
      });
      this.$axMethod.DefectiveProductExportOrderDetailed(s).then(res => {
        if (res.data.code == 200) {
          window.location.href = res.data.data.url;
        }
      });
    }
  },
  mounted() {
    opthionList(this, window.localStorage);
    this.authority = JSON.parse(localStorage.getItem("authority"));
  }
};
</script>

